import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { GnosisClientModel, GnosisSafe } from './gnosis-client.model'
import {
  GNOSIS_CLIENT_ENDPOINT,
  GNOSIS_SAFE_ABI,
  GNOSIS_SIGNATURE_VERIFICATION_CODE,
} from './gnosis-client.const'
import { isAddress } from '../core'
import { hashMessage } from 'web3-eth-accounts'
import { Web3Clients } from '../web3-client'

/**
 * API Client class
 */
export class GnosisClient implements GnosisClientModel {
  // Private
  private safeAddress: string | undefined

  // Web3 instance
  public web3: Web3Clients

  // Axios instance
  private axios: Axios

  constructor(web3: Web3Clients, safeAddress?: string) {
    this.web3 = web3
    this.safeAddress = safeAddress
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      baseURL: GNOSIS_CLIENT_ENDPOINT,
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  /**
   * Get safe address to use
   * @param safeAddress safe address
   * @returns safe address
   */
  private getSafeAddress(safeAddress?: string) {
    if (!safeAddress) {
      safeAddress = this.safeAddress
    }

    if (!safeAddress || !isAddress(safeAddress)) {
      throw Error(`Safe address malformed: ${safeAddress}`)
    }

    return safeAddress
  }

  /**
   * Verify gnosis safe signature hash
   * @param message message to sign
   * @param hash signed message hash
   * @param safeAddress safe address
   * @returns true | false
   */
  public async verifySignature(
    message: string,
    hash: string,
    safeAddress?: string
  ): Promise<boolean> {
    safeAddress = this.getSafeAddress(safeAddress)

    // Check safe signature for each chain
    const messageHash = hashMessage(message)
    const chainIds = Object.keys(this.web3)
    for await (const chainId of chainIds) {
      const web3Client = this.web3[chainId]
      const gnosisSafeContract = web3Client.initContract(
        GNOSIS_SAFE_ABI,
        safeAddress
      )

      try {
        const signatureAddress: string = await gnosisSafeContract.methods[
          'isValidSignature'
        ](messageHash, hash).call({ from: safeAddress })
        if (signatureAddress === GNOSIS_SIGNATURE_VERIFICATION_CODE) {
          return true
        }
      } catch (err) {
        continue
      }
    }
    return false
  }

  /**
   * Get gnosis safe info from APIs
   * @param safeAddress safe address
   * @returns Safe info if any
   */
  public async getSafe(safeAddress?: string): Promise<GnosisSafe | undefined> {
    safeAddress = this.getSafeAddress(safeAddress)

    const response = await this.axios.request({
      url: safeAddress,
      method: 'GET',
      responseType: 'json',
    })

    return response?.data
  }
}
